* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 20px;
  font-family: Arial, sans-serif;
}

.container {
  max-width: 800px;
  margin: 0 auto;
}

h1 {
  text-align: center;
  margin-bottom: 20px;
}

.search-form {
  margin-bottom: 20px;
  display: flex;
}

.search-form input[type="text"] {
  border: 1px solid #ccc;
  border-radius: 4px;
  outline: none;
  flex: 1;
  padding: 10px;
  font-size: 16px;
}

.search-form button {
  color: #fff;
  cursor: pointer;
  background-color: #333;
  border: none;
  border-radius: 4px;
  padding: 10px 20px;
  font-size: 16px;
}

.gallery {
  flex-wrap: wrap;
  margin-bottom: 20px;
  display: flex;
}

.photo-card {
  width: calc(25% - 20px);
  background-color: #f7f7f7;
  border: 1px solid #ccc;
  border-radius: 4px;
  margin: 10px;
  overflow: hidden;
  box-shadow: 0 2px 4px rgba(0, 0, 0, .1);
}

.photo-card img {
  width: 100%;
  height: auto;
}

.photo-card .info {
  padding: 10px;
}

.photo-card .info-item {
  margin: 5px 0;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #333;
  border: none;
  border-radius: 4px;
  padding: 10px 20px;
  font-size: 16px;
  display: none;
}

.notification {
  color: #fff;
  z-index: 9999;
  background-color: #ff4d4d;
  border-radius: 4px;
  padding: 10px;
  position: fixed;
  top: 20px;
  right: 20px;
}

